* {
  margin: 0;
  padding: 0;
}

html, body{
  font-size: 18px;
  background-color: #f5f6f7;
}

h1{
  font-size: 2.5em;
  font-family: sans-serif;
  font-weight: 700;
  line-height: 1.1;
  margin: 40px 0;
  color: #fff;
}

.App{
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: black;
}

.contenedor-principal {
  min-height: 600px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.imagen-logo {
  height: 20%;
  width: 20%;
}

.div {
  background-color: #ffffffed;
}

.logossb{
  height: 100%;
  width: auto;
}
